import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      component: () => import('./views/Index.vue'),
    },
    {
      path: '/portfolio',
      component: () => import('./views/Portfolio.vue'),
    },
    {
      path: '/contact',
      component: () => import('./views/Contact.vue')
    }
  ]
})
